import Vue from 'vue'
import Router from 'vue-router'
const Index = ()=> import('@/pages/index')//首页
const Result = ()=> import('@/pages/result')//结果页
const Issue = ()=> import('@/pages/issue')//问题页
const IssueList = ()=> import('@/pages/issueList')//问题列表

// 百年
const BN = ()=> import('@/pages/BN/index')//首页
const Result1 = ()=> import('@/pages/BN/result')//结果页
const Issue1 = ()=> import('@/pages/BN/issue')//问题页
const IssueList1 = ()=> import('@/pages/BN/issueList')//问题列表

// //中信宝
const Index2 = ()=> import('@/pages/zhongXB/index-2')//首页
const Issue2 = ()=> import('@/pages/zhongXB/issue-2')//问题
const Result2 = ()=> import('@/pages/zhongXB/result-2') //结论

//多啦A宝
const Index3 = ()=> import('@/pages/duolaA/index')//首页
const Result3 = ()=> import('@/pages/duolaA/result')//结果页
const Issue3 = ()=> import('@/pages/duolaA/issue')//问题页
const IssueList3 = ()=> import('@/pages/duolaA/issueList')//问题列表

const Index4 = ()=> import('@/pages/haibao/index')//首页
const Result4 = ()=> import('@/pages/haibao/result')//结果页
const Issue4 = ()=> import('@/pages/haibao/issue')//问题页
const IssueList4 = ()=> import('@/pages/haibao/issueList')//问题列表

//复兴
const Index5 = () => import('@/pages/fuxing/index')//首页
const Index6 = () => import('@/pages/fuxing/index1')//二级疾病页面
const Issue5 = () => import('@/pages/fuxing/issue')//问题页
const Result5 = () => import('@/pages/fuxing/result')//结果页
const Healthy5 = () => import('@/pages/fuxing/healthy')//健康告知页
const Healthy6 = () => import('@/pages/fuxing/healthy1')
const Form5 = () => import('@/pages/fuxing/form')//填单页面
const Question5 = () =>import('@/pages/fuxing/question')//回答问题界面
const Finish5 = () =>import('@/pages/fuxing/finish')//结束健康告知界面
const Questionnaire5 = () =>import('@/pages/fuxing/questionnaire')//低保额问卷
const Upload1 = () =>import('@/pages/fuxing/upload1')//上传资料1
const Upload2 = () =>import('@/pages/fuxing/upload2')//上传资料2
const Upload3 = () =>import('@/pages/fuxing/upload3')//上传资料3
const Waiting = () =>import('@/pages/fuxing/waiting')//等待结果

Vue.use(Router)
export default new Router({
  routes: [
    //复兴交互式核保--低保额问卷
    {
      path:'/fuxing/questionnaire',
      component:Questionnaire5,
      name:'Questionnaire5',
      meta:{
        title:'智能核保'
      }
    },
    //复兴交互式核保--上传资料1
    {
      path:'/fuxing/upload1',
      component:Upload1,
      name:'Upload1',
      meta:{
        title:'智能核保'
      }
    },
    // //复兴交互式核保--上传资料2
    // {
    //   path:'/fuxing/upload2',
    //   component:Upload2,
    //   name:'Upload2',
    //   meta:{
    //     title:'智能核保'
    //   }
    // },
    //复兴交互式核保--上传资料3
    {
      path:'/fuxing/upload3',
      component:Upload3,
      name:'Upload3',
      meta:{
        title:'智能核保'
      }
    },
    // //复兴交互式核保--健康告知
    // {
    //   path:'/fuxing/healthy',
    //   component:Healthy5,
    //   name:'Healthy5',
    //   meta:{
    //     title:'智能核保'
    //   }
    // },
    // {
    //   path:'/fuxing/healthy1',
    //   component:Healthy6,
    //   name:'Healthy6',
    //   meta:{
    //     title:'智能核保'
    //   }
    // },
    //复兴交互式核保--选择疾病
    {
      path:'/fuxing/index',
      component:Index5,
      name:'Index5',
      meta:{
        title:'智能核保'
      }
    },
    //选择疾病 二级疾病
    {
      path:'/fuxing/index1',
      component:Index6,
      name:'Index6',
      meta:{
        title:'智能核保'
      }
    },
    //复兴交互式核保--选择完疾病之后进入回答问题界面
    {
      path:'/fuxing/question',
      component:Question5,
      name:'Question5',
      meta:{
        title:'智能核保'
      }
    },
    //复兴交互式核保--问题确认页
    {
      path:'/fuxing/issue',
      component:Issue5,
      name:'Issue5',
      meta:{
        title:'智能核保'
      }
    },
    //复兴交互式核保--健康告知结束页
    // {
    //   path:'/fuxing/finish',
    //   component:Finish5,
    //   name:'Finish5',
    //   meta:{
    //     title:'智能核保'
    //   }
    // },
    //复兴交互式核保--审核成功进入投保填单页面
    // {
    //   path:'/fuxing/form',
    //   component:Form5,
    //   name:'Form5',
    //   meta:{
    //     title:'智能核保'
    //   }
    // },
    //复兴交互式核保--结论页
    {
      path:'/fuxing/result',
      component:Result5,
      name:'Result5',
      meta:{
        title:'核保结论'
      }
    },
    //复兴交互式核保--等待结果页
    {
      path:'/fuxing/waiting',
      component:Waiting,
      name:'Waiting',
      meta:{
        title:'核保结论'
      }
    },
    // // 首页
    {
      path: '/index/:pageLevel/:desLevel',
      component: Index,
      name:'Index',
      meta: {
        title: '智能核保',
      }
    },
    {
      path: '/result',
      component: Result,
      name:'Result',
      meta: {
        title: '智能核保'
      }
    },
    {
      path: '/issue/:pageLevel/:desLevel',
      component: Issue,
      name:'Issue',
      meta: {
        title: '智能核保'
      }
    },
    {
      path: '/issueList',
      component: IssueList,
      name:'IssueList',
      meta: {
        title: '智能核保'
      }
    },
    // 百年智能核保
    {
      path: '/BNIndex/:pageLevel?/:desLevel?',
      component: BN,
      name:'BN',
      meta: {
        title: '智能核保',
      }
    },
    {
      path: '/BN/issue/:pageLevel/:desLevel/:code/:anCode?',
      component: Issue1,
      name:'Issue1',
      meta: {
        title: '智能核保'
      }
    },
    {
      path: '/BN/issueList/:desLevel',
      component: IssueList1,
      name:'IssueList1',
      meta: {
        title: '智能核保'
      }
    },
    {
      path: '/BN/result',
      component: Result1,
      name:'Result1',
      meta: {
        title: '智能核保'
      }
    },
    //中信宝
     // 首页
     {
      path: '/index-2',
      component: Index2,
      name:'Index2',
      meta: {
        title: '智能核保',
      }
    },
    {
      path: '/issue-2',
      component: Issue2,
      name:'Issue2',
      meta: {
        title: '智能核保'
      }
    },
    {
      path: '/result-2',
      component: Result2,
      name:'Result2',
      meta: {
        title: '核保结论'
      }
    },
      // 首页
    {
      path: '/index/:pageLevel/:desLevel',
      component: Index,
      name:'Index',
      meta: {
        title: '智能核保',
      }
    },
    {
      path: '/result',
      component: Result,
      name:'Result',
      meta: {
        title: '智能核保'
      }
    },
    {
      path: '/issue/:pageLevel/:desLevel',
      component: Issue,
      name:'Issue',
      meta: {
        title: '智能核保'
      }
    },
    {
      path: '/issueList',
      component: IssueList,
      name:'IssueList',
      meta: {
        title: '智能核保'
      }
    },
    //多啦A宝
    {
      path: '/duola/index/:pageLevel/:desLevel',
      component: Index3,
      name:'Index3',
      meta: {
        title: '智能核保',
      }
    },
    {
      path: '/duola/result',
      component: Result3,
      name:'Result3',
      meta: {
        title: '智能核保'
      }
    },
    {
      path: '/duola/issue/:pageLevel/:desLevel/:code',
      component: Issue3,
      name:'Issue3',
      meta: {
        title: '智能核保'
      }
    },
    {
      path: '/duola/issueList/:desLevel',
      component: IssueList3,
      name:'IssueList3',
      meta: {
        title: '智能核保'
      }
    },
    // 海宝
    {
      path: '/index4/:pageLevel/:desLevel',
      component: Index4,
      name:'Index4',
      meta: {
        title: '智能核保',
      }
    },
    {
      path: '/result4',
      component: Result4,
      name:'Result4',
      meta: {
        title: '智能核保'
      }
    },
    {
      path: '/issue4/:pageLevel/:desLevel',
      component: Issue4,
      name:'Issue4',
      meta: {
        title: '智能核保'
      }
    },
    {
      path: '/issueList4',
      component: IssueList4,
      name:'IssueList4',
      meta: {
        title: '智能核保'
      }
    },
  ]
})



