//获取AJAX请求 数据
function getHttp(param,success,failure){
    let data=apiList[param.name];
    var params;
    var url = data.url;
    if(!data){
        alert("未找到此对象")
        return
    }
    if (param.header == 2 && param.params){
        params = getURLSearchParams(param.params)
        console.log(params)
    }
    else if (param.header == 2){
        params = getURLSearchParams(data.params)
        console.log(params)
    }else if(param.params){
        params=param.params
    }else{
        params=data.params;
    }
    if(param.code){
        url = url+param.code
    }
    if(param.orderId){
        url = url+param.orderId
    }
    console.log(data.type)
    if(!data.type || data.type=="post"){
        ajax.post(url,params,success,failure);
    }else if(data.type=="get"){
        ajax.get(url,params,success,failure);
    }
    else if(data.type=="put"){
        ajax.put(url,params,success,failure);
    }
}

export default {
    getHttp
}


