const filter = {
  // 字符串截取
  subString(value, length) {
    if (!value) {
      return "";
    }
    value = value.trim();
    if (value.length > length) {
      var first = value.toString().substring(0, length);
      value = first + "...";
    }
    return value;
  },
  // 数字处理
  numberFormat(value) {
    value = parseInt(value);
    if (isNaN(value)) {
      return 0;
    }

    if (value >= 10000) {
      value = value + "";
      var len = value.length;
      value = value.slice(0, len - 3);
      return parseFloat(value / 10) + "万";
    } else {
      return value
    }
  },
  // 内容\n换成回车
  fmtContent(value) {
    value = value.split("\n").join("<br/>");
    return value;
  },
  //根据产品类型判断是否显示保障金额
  guaranteeLimit(content) {
    if (!content || !content.insuranceProductName) return "";
    var val = "";
    if (content.insuranceProductName.indexOf("重疾") > -1 || content.insuranceProductName.indexOf("重大疾病") > -1) {
      val = content.guaranteeLimit + "元";
    } else {
      val = "详见保障权益"
    }
    return val;
  },
  // 日期字数控制
  timeNum(content, num) {
    if (!content) return;
    if (num < content.length) return content;
    return content.slice(0, num);
  }
}









export {
  filter
}



