
import Vue from 'vue'
import layer from 'static/js/layer/layer' //弹出层
import 'static/js/layer/need/layer.css' //弹出层样式
let popup=(function (){
    return {
        msg: function(text){
            //中间轻提示框
            layer.open({
                content: text
                ,skin: 'msg'
                ,styles: 'bottom:0;'
                ,time: 2 //2秒后自动关闭
              });
        },
        alert:function(text,btn){
            //ios风格确认按钮弹窗
            layer.open({
                content: text
                ,skin:'lzAlert'
                ,btn: btn || '我知道了'
              });
        },
        footer:function(text){
            //底部提示框
            layer.open({ 
                content: text
                ,skin: 'footer'
              });
        },
        loading:function(text){
            //加载中
            var loading=layer.open({
                type: 2
                ,content: text
                ,shadeClose:false
              });
              return loading
        },
        /**
         * 询问框弹窗
         * text-文字
         * btns-按钮文本
         * yes-成功回调
         * no-失败回调
         */
        inquiry:function(text,btns,yes,no){
            var inquiry=layer.open({
                content: text
                ,btn: btns
                ,skin:"inquiry"
                ,shadeClose:false
                ,yes: function(index){
                    layer.close(inquiry);
                  yes(index);
                },
                no:function(){
                    if(no){
                        no();
                    }
                    
                }
              });
        },
        closeLay:function(index){
            layer.close(index)
        }
    }
})()


export default popup


