import { log } from 'util';
import common from './common';
import popup from './popup';
require('babel-polyfill')
// import layer from 'vue2-layer-mobile' //弹出层

// 配置API接口地址
var root = ''
var root = common.api_root
console.log(root)

require('es6-promise').polyfill();
// 引用axios
var axios = require('axios')
// 自定义判断元素类型JS
function toType(obj) {
    return ({}).toString.call(obj).match(/\s([a-zA-Z]+)/)[1].toLowerCase()
}
// 参数过滤函数
function filterNull(o) {
    for (var key in o) {
        if (o[key] === null) {
            delete o[key]
        }
        if (toType(o[key]) === 'string') {
            console.log(o[key])
            o[key] = o[key].trim()
        } else if (toType(o[key]) === 'object') {
            o[key] = filterNull(o[key])
        } else if (toType(o[key]) === 'array') {
            o[key] = filterNull(o[key])
        }
    }
    return o
}
/*
  接口处理函数
  这个函数每个项目都是不一样的，我现在调整的是适用于
  https://cnodejs.org/api/v1 的接口，如果是其他接口
  需要根据接口的参数进行调整。参考说明文档地址：
  https://cnodejs.org/topic/5378720ed6e2d16149fa16bd
  主要是，不同的接口的成功标识和失败提示是不一致的。
  另外，不同的项目的处理方法也是不一致的，这里出错就是简单的alert
*/

function apiAxios(method, url, params,success, failure) {
    if (params) {
        // params = filterNull(params)
    }
    axios({
        method: method,
        url: url,
        data: method === 'POST' || method === 'PUT' ? params : null,
        params: method === 'GET' || method === 'DELETE' ? params : null,
        baseURL: root,
        withCredentials: true
    })
        .then(function (res) {
            if (success && params.flag) {
                var isError = successError1(res.data);
                success(res.data,isError)
            }
            else if(success){
                var isError = successError(res.data);
                success(res.data,isError)
            }
            
        })
        .catch(function (err) {
            let res = err.response 
            if (res) {
                errordata(res.status)
            }
        })
}

//请求错误处理
function errordata(data) {
    if (data.status == 502) {
        popup.msg('服务器出错，请稍后再试-502')
        return false;
    } else if (data.status == 0) {
        popup.msg('未联网，请先检查网络')
        return false;
    }
    return 1;
}

 //公共错误处理 
 function successError1(data) { //新框架暂定 httpCode=1 为成功标志
    //服务器出错
    if (data.httpCode != 1 && data.data) {
        popup.alert(data.data.errorMsg)
        return false;
    }else if(data.httpCode != 1){
        popup.alert(data.msg)
        return false;
    }
    return true
}

 //公共错误处理
 function successError(data) {
    //服务器出错
    if (data.httpCode != 200 && data.data) {
        popup.alert(data.data.errorMsg)
        return false;
    }else if(data.httpCode != 200){
        popup.alert(data.msg)
        return false;
    }
    return true
}

// 返回在vue模板中的调用接口
export default {
    get: function (url, params, success, failure) {
        return apiAxios('GET', url, params, success, failure)
    },
    post: function (url, params, success, failure) {
        return apiAxios('POST', url,params, success, failure)
    },
    put: function (url, params, success, failure) {
        return apiAxios('PUT', url, params, success, failure)
    },
    delete: function (url, params, success, failure) {
        return apiAxios('DELETE', url, params, success, failure)
    }
}


