import * as types from './mutation-types'
const mutaions = {
    //提交state 数据方法
    [types.SET_MESSAGE](state,payload){
        if(payload.lev){
            state[payload.key][payload.lev] = payload.data
        }else{
            state[payload.key] = payload.data
        }
    }
}
export default mutaions


