// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import rem from '../static/js/flexible.js' //rem转换
import http from './api/httpList.js'  //ajax请求
import tool from './api/motheds.js'  //工具
import popup from './api/popup.js'  //工具
import common from './api/common.js'  //工具
import title from 'vue-wechat-title'  //工具
import * as filter from './api/filter.js' //公共过滤器
import store from './store'
import 'url-search-params-polyfill' // 解决URLSearchParams ios10.3以下版本的兼容性
import Mint from 'mint-ui';
import Vconsole from 'vconsole';
Vue.config.productionTip = false
//公共sass文件
import './sass/helper/_variables.scss';
import './sass/helper/_function.scss';
import '../static/iconfont/iconfont.css';
import 'mint-ui/lib/style.css';
if(common.env == '0'){
  let vConsole = new Vconsole();
  Vue.use(vConsole);
}

Vue.use(rem)
// Vue.use(ajax)
Vue.use(http)
Vue.use(tool)
Vue.use(popup)
Vue.use(filter)
Vue.use(title)
Vue.use(Mint)

const filters = filter.filter;
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})

// Vue.directive('title', {
//   inserted: function (el, binding) {
//     document.title = binding.value;
//   },
//   update:function (el, binding) {
//     document.title = binding.value;
//   }
// });
// 将API方法绑定到全局
// Vue.prototype.$ajax = ajax
Vue.prototype.$http = http
Vue.prototype.$tool = tool
Vue.prototype.$popup = popup
Vue.prototype.$store = store
Vue.prototype.$common = common
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



