import motheds from "../api/motheds";
import {SET_MESSAGE} from './mutation-types' // 函数方法名称
//处理本地缓存插件 
const myPlugins = (socket)=>{
    return store =>{
        Object.keys(store.state).map(key=>{
            if(motheds.tools().getsion(key)){
                store.commit(SET_MESSAGE,{key:key,data:motheds.tools().getsion(key)})
            }
        })
        store.subscribe((mutation,state)=>{
            Object.keys(store.state).map(key=>{
                motheds.tools().setsion(key,state[key])
            })
        })
    }
}
export default myPlugins


