import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import mutations from './mutations'
import getters from './getters'
import * as types from './mutation-types'
import myPlugins from './myPlugins'

Vue.use(Vuex)
export default new Vuex.Store({
    state,
    getters,
    plugins:[myPlugins()],
    mutations,
})


